<template>
	<div style="width:100%;height:100%;">
		<div class="max_box">
			<div class="nav">
				<div class="left_logo">
					<img src="../assets/img/logo.png" />
				</div>
				<div class="center">
					<ul>
						<li class="one">布控管理</li>
					</ul>
				</div>
				<div class="right_user">
					<div class="user_box">
						<div class="user_icon"><img src="../assets/img/user_icon.png" /></div>
						<div class="user_text">admin</div>
					</div>
					<div class="cancellation">注销</div>
				</div>
			</div>
			<div class="contentbox">
				<div class="mask_box">
					<div class="top_title">
						<div class="title_lefttext">设备配置</div>
						<div class="title_righttext">结果 {{init_data.allnum}} 个</div>
					</div>
					<div class="input_box">
						<div class="export_btn" @click="click_to_add_info" >添加设备</div>
						 <div class="right_btn h2_right_btn m3_right_box">
							<div class="search h2_search" @click="click_to_search">搜索</div>
							<div class="mm4_prompt">Mac地址：</div>
							<input class="center_input id_card input_right" type="text" v-model="search_data.macAddress" placeholder="00-01-6C-06-A6-21" />
						    <div class="mm4_prompt">推送IP：</div>
						    <div class="mm4_numberbox">
						    	<input maxlength="3" class="center_input mm4_number" type="text" v-model="search_data.pushIp[0]"/>
						    	<div class="number_dian">.</div>
						    	<input maxlength="3" class="center_input mm4_number" type="text" v-model="search_data.pushIp[1]"/>
						    	<div class="number_dian">.</div>
						    	<input maxlength="3" class="center_input mm4_number" type="text" v-model="search_data.pushIp[2]"/>
						    	<div class="number_dian">.</div>
						    	<input maxlength="3" class="center_input mm4_number" type="text" v-model="search_data.pushIp[3]"/>
						    	<div class="limit_text">只可输入0~255的数字</div>
						    </div>
						    
						 </div> 
					</div>
					<div class="table_box h2_table_box">
						<div class="table_thbox">
							<table>
								<tr>
									<td class="td td1">
										<input class="checkbox_box" type="checkbox" :checked="isallchecked" v-model="isallchecked" @click="click_to_checkedall"/>
									</td>
									<td class="td td4">编号</td>
									<td class="td td4">设备名称</td>
									<td class="td td4">Mac地址</td>
									<td class="td td5">推送IP</td>
									<td class="td td5">阈值</td>
									<td class="td td4">操作</td>
								</tr>
							</table>
						</div>
						<div class="table_thbox2">
							<table>
								<tr class="tr" v-for="item,index in tabledata">
									<td class="td td1">
										<input class="checkbox_box" type="checkbox"  :checked="item.ischecked" v-model="item.ischecked" @click="click_to_checkedone(index)"/>
									</td>
									<td class="td td4">
										<div class="table_text">
											<div class="cell_text">
												{{ item.id }}
											</div>
										</div>
									</td>
									<td class="td td4">
										<div class="table_text">
											<div class="cell_text">
												{{ item.name }}
											</div>
										</div>
									</td>
									<td class="td td4">
										<div class="table_text">
											<div class="cell_text">
												{{ item.macAddress }}
											</div>
										</div>
									</td>
									<td class="td td5">
										<div class="table_text">
											<div class="cell_text">
												{{ item.pushIp }}
											</div>
										</div>
									</td>
									<td class="td td5">
										<div class="table_text">
											<div class="cell_text">
												{{ item.threshold }} 
											</div>
										</div>
									</td>
									<td class="td td4">
										<div class="icon_fa">
	                                    	<div class="td_icon2 systd_icon2 icon2" @click="click_to_update_person(item.id,index)" title="修改用户信息"></div>
		                                    <div class="td_icon2 systd_icon2 icon_trash" 
		                                    	 @click="click_to_delete_person(item.id)" 
		                                    	 title="删除用户信息">
		                                    </div>
	                                    </div>   
									</td>
								</tr>
							</table>
						</div>

						<div class="pag">
							<el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="init_data.pageNum" :page-sizes="[10, 20, 30, 50]" :page-size="init_data.pageSize" layout="total, sizes, prev, pager, next, jumper" :total="init_data.allnum" class="haha">
							</el-pagination>
						</div>
					</div>
				</div>
			</div>
		</div>
		
		<!--0911 add start-->	
		<!--遮罩层-->
		<div class="mack_box" v-show="is_show_info"></div>
		<!--弹出框-添加设备-->
		<div class="bounced_add mm1_bounced" v-show="is_show_info">
			<div class="bounced_box">
				<div class="bounced_top">
					<div class="bounced_title">添加设备</div>
				</div>
				<div class="mm1_addbox1">
					<div class="addbox1_text">设备名称</div>
					<input class="mm1_inputname mm1_inputname1" type="text" v-model="showdata.name"/>
					<input class="mm1_inputname" type="text" v-model="showdata.macAddress"/>
					<span>Mac地址：</span>
				</div>
				<div class="mm1_addbox1">
					<div class="addbox1_text">推送IP</div>
					<input class="mm1_inputname mm1_inputname1" type="text" v-model="showdata.pushIp"/>
					
				</div>
				<div class="mm1_addbox1">
					<div class="similar_box">
						<div class="addbox1_text">阈值</div>
						<div class="slider_box">
							<el-slider 
								v-model="showdata.threshold"
								:max= "1"
								:step= "0.01">
							</el-slider>
						</div>
						<div class="percentage"><input type="text" value="0.75" v-model="showdata.threshold"/></div>
						<!-- <div class="percentage_text">%</div> -->
					</div>
				</div>
				<div class="mm1_addbox2">
					<div class="left_colordiv"></div>
					<div class="right_btndiv">
						<div class="mmbtn_box mm1_btn" @click="confirm_to_add_camera" v-if="model === 'add'">保存</div>
						<div class="mmbtn_box mm1_btn" @click="confirm_to_update_camera" v-else>保存</div>
						<div class="mmbtn_box left_mmbox mm1_btn" @click="click_to_clear">关闭</div>
					</div>
				</div>
			</div>
		</div>
		<!--0911 add end-->	
	</div>
</template>

<script>
	export default {
		data(){
			return{
				init_data: {
					pageNum: 1,
					pageSize: 10,
					allnum: 0,
				},

				// tabledata:[],
				tabledata:[
					{
						id: 6,
				        name: "test01",
				        macAddress: "00-01-6C-06-A6-21",
				        pushIp: "127.0.0.1",
				        threshold: "0.75",
				        ischecked: false,
					},
					{
						id: 6,
				        name: "test01",
				        macAddress: "00-01-6C-06-A6-21",
				        pushIp: "127.0.0.1",
				        threshold: "0.75",
				        ischecked: false,
					},
					{
						id: 6,
				        name: "test01",
				        macAddress: "00-01-6C-06-A6-21",
				        pushIp: "127.0.0.1",
				        threshold: "0.75",
				        ischecked: false,
					},
					{
						id: 6,
				        name: "test01",
				        macAddress: "00-01-6C-06-A6-21",
				        pushIp: "127.0.0.1",
				        threshold: "0.75",
				        ischecked: false,
					},
				],

				search_data:{
					macAddress:"",
					pushIp: ['', '', '', ''],
				},

				// 复选框
				isallchecked: false,

				// 弹窗
				is_show_info: false,

				// 弹窗信息
				model: "add",
				showdata:{
					name: "",
					macAddress: "",
					pushIp: "",
					threshold: 0.75,
				},
			}
		},
		methods: {
			// 页面切换函数
			handleSizeChange:function(val) {
				this.isallchecked = false
				// console.log(val)
				this.init_data.pageSize = val
				this.get_data_list({})
			},
			handleCurrentChange:function(val) {
				this.isallchecked = false
				this.init_data.pageNum = val
				this.get_data_list({})
			},

			// 输入-校验
            check_input:function(input_data,model=""){
                let reg = /^.{0,20}$/

                if( model === "ip" ){
                	reg = /^(1\d{2}|2[0-4][0-9]|25[0-5]|[1-9][0-9]|[1-9])(\.(1\d{2}|2[0-4][0-9]|25[0-5]|[1-9][0-9]|[0-9])){3}$/
                }else if( model === "mac" ){
                	reg = /^[A-F0-9]{2}(-[A-F0-9]{2}){5}$/
                }

                return reg.test(input_data)
            },

			// 点击事件
			// 修改事件
			click_to_update_person:function( id,index ){
				this.model = "update"
				this.showdata = JSON.parse(JSON.stringify(this.tabledata[index]))
				this.showdata.uuid = index
				this.is_show_info = true
			},
			click_to_delete_person:function( id ){
				this.$confirm('是否删除该设备信息？','提示',{
					confirmButtonText: '是',
		            cancelButtonText: '否',
		            type: 'warning'
				}).then(() => {
					this.require_to_delete( id )
				}).catch(() => {
					;
				})
			},
			// 搜索信息
			click_to_search:function(){
				let search_data = {}
				if( this.search_data.macAddress != "" ){
					search_data.macAddress = this.search_data.macAddress
				}
				if( this.search_data.pushIp.join(".") != "..." ){
					search_data.pushIp = this.search_data.pushIp.join(".")
				}

				// if( !this.check_input(search_data.macAddress,"mac") && search_data.macAddress ){
				// 	this.warning_info("mac地址格式不正确")
				// }
				// if( !this.check_input(search_data.pushIp,"ip") && search_data.pushIp){
				// 	this.warning_info("ip地址格式不正确")
				// }
				this.get_data_list(search_data)
			},	
			// 添加弹窗显示
			click_to_add_info:function(){
				this.model = "add"
				this.is_show_info = true
			},
			// 添加设备事件
			confirm_to_add_camera:function(){
				for( let item in this.showdata ){
					if( this.showdata[item] === "" ){
						if( item === "name" ){
							this.warning_info( "设备名称不能为空" )
							return ;
						}else if( item === "macAddress" ){
							this.warning_info( "Mac地址不能为空" ) 
							return ;
						}else if( item === "pushIp" ){
							this.warning_info( "推送IP不能为空" )
							return ;
						}
					}
				}
				if( !this.check_input(this.showdata.name) ){
					this.warning_info("设备名称不能超过20个字符")
					return ;
				}
				if( !this.check_input(this.showdata.macAddress,"mac")){
					this.warning_info("mac地址格式不正确")
					return ;
				}
				if( !this.check_input(this.showdata.pushIp,"ip")){
					this.warning_info("ip地址格式不正确")
					return ;
				}
				this.require_to_add(this.showdata)
				// this.is_show_info = false
			},
			// 修改设备信息
			confirm_to_update_camera:function(){
				for( let item in this.showdata ){
					if( this.showdata[item] === "" ){
						if( item === "name" ){
							this.warning_info( "设备名称不能为空" )
							return ;
						}else if( item === "macAddress" ){
							this.warning_info( "Mac地址不能为空" ) 
							return ;
						}else if( item === "pushIp" ){
							this.warning_info( "推送IP不能为空" )
							return ;
						}
					}
				}
				if( !this.check_input(this.showdata.name) ){
					this.warning_info("设备名称不能超过20个字符")
					return ;
				}
				if( !this.check_input(this.showdata.macAddress,"mac")){
					this.warning_info("mac地址格式不正确")
					return ;
				}
				if( !this.check_input(this.showdata.pushIp,"ip")){
					this.warning_info("ip地址格式不正确")
					return ;
				}
				let index = this.showdata.uuid
				let update_data = {}
				if( this.showdata.name === this.tabledata[index].name 
					&& this.showdata.macAddress === this.tabledata[index].macAddress
					&& this.showdata.pushIp === this.tabledata[index].pushIp
					&& this.showdata.threshold === this.tabledata[index].threshold){
					this.warning_info( "设备信息未变更" )
					return ;
				}
				if( this.showdata.name != this.tabledata[index].name ){
					update_data.name = this.showdata.name
				}
				if( this.showdata.macAddress != this.tabledata[index].macAddress ){
					update_data.macAddress = this.showdata.macAddress
				}
				if( this.showdata.pushIp != this.tabledata[index].pushIp ){
					update_data.pushIp = this.showdata.pushIp
				}
				if( this.showdata.threshold != this.tabledata[index].threshold ){
					update_data.threshold = this.showdata.threshold
				}

				this.require_to_update(update_data)
				// this.is_show_info = false
			},
			// 清除事件、关闭弹窗
			click_to_clear:function(){
				this.is_show_info = false
				this.showdata = {
					name: "",
					macAddress: "",
					pushIp: "",
					threshold: 0.75,
				}
			},

			// 请求
			// 请求数据列表
			get_data_list:function( search_data ){

				search_data.pageNum = this.init_data.pageNum
    			search_data.pageSize = this.init_data.pageSize

				// 设备组列表请求
				this.$ajax.get("/camera",{ params: search_data }).then((res) => {
                    if( res.data.status === 0){
            			this.init_data.allnum = res.data.data.totalElements
            			this.tabledata = res.data.data.content

            			for( let i = 0; i < this.tabledata.length; i++ ){
            				this.tabledata[i].ischecked = false
            			}
                    }else if( res.data.status === 1 ){
	                    this.error_info(res.data.msg)
                    	return ;
                    }else if( res.data.status === 2 ){
	                    this.error_info(res.data.msg)
                    	return ;
                    }else if( res.data.status === 10 ){
	                    this.error_info('请先登录')
                    	return ;
                    }else{
                    	this.error_info(res.data.status + "  " + res.data.msg)
                    }
                }).catch((error) => {
                	this.error_info('网络连接出错')
                    return ;
                })
			},
			// 删除数据
			require_to_delete:function( id ){
				// 设备组列表请求
				this.$ajax.delete("/camera/"+id).then((res) => {
                    if( res.data.status === 0){
            			this.success_info( "删除成功" )
            			this.get_data_list({})
                    }else if( res.data.status === 1 ){
	                    this.error_info(res.data.msg)
                    	return ;
                    }else if( res.data.status === 2 ){
	                    this.error_info(res.data.msg)
                    	return ;
                    }else if( res.data.status === 10 ){
	                    this.error_info('请先登录')
                    	return ;
                    }else{
                    	this.error_info(res.data.status + "  " + res.data.msg)
                    }
                }).catch((error) => {
                	this.error_info('网络连接出错')
                    return ;
                })
			},
			// 修改数据
			require_to_update:function( update_data ){
				this.$ajax.put("/camera",update_data).then((res) => {
                    if( res.data.status === 0){
	                    this.success_info('设备信息修改成功')
	                    this.click_to_clear()
	                  	this.get_data_list({})
                    }else if( res.data.status === 1 ){
	                    this.error_info(res.data.msg)
                    	return ;
                    }else if( res.data.status === 2 ){
	                    this.error_info(res.data.msg)
                    	return ;
                    }else if( res.data.status === 10 ){
	                    this.error_info('请先登录')
                    	return ;
                    }else{
                    	this.error_info(res.data.status + "  " + res.data.msg)
                    }
                    this.is_confirm_show = true
                }).catch((error) => {
                	console.log(error)
                    this.error_info('网络连接出错')
                    this.is_confirm_show = true
                    return ;
                })
			},
			// 添加设备信息
			require_to_add:function( add_data ){

				var params = new URLSearchParams()
				for( let item in add_data ){
					params.append( item, add_data[item] )
				}

				this.$ajax.post("/camera",params).then((res) => {
                    if( res.data.status === 0){
	                    this.success_info('设备添加成功')
	                    this.click_to_clear()
	                  	this.get_data_list({})
                    }else if( res.data.status === 1 ){
	                    this.error_info(res.data.msg)
                    	return ;
                    }else if( res.data.status === 2 ){
	                    this.error_info(res.data.msg)
                    	return ;
                    }else if( res.data.status === 10 ){
	                    this.error_info('请先登录')
                    	return ;
                    }else{
                    	this.error_info(res.data.status + "  " + res.data.msg)
                    }
                    this.is_confirm_show = true
                }).catch((error) => {
                	console.log(error)
                    this.error_info('网络连接出错')
                    this.is_confirm_show = true
                    return ;
                })
			},

			// 消息窗口
			error_info:function(mes){
				this.is_confirm_show = true
				this.$message({
                    type: 'error',
                    message: mes,
                    showClose: true,
                    center: true
                })
			},
			warning_info:function(mes){
				this.$message({
                    type: 'warning',
                    message: mes,
                    showClose: true,
                    center: true
                })
			},
			success_info:function(mes){
				this.$message({
                    type: 'success',
                    message: mes,
                    showClose: true,
                    center: true
                })
			},

			// 复选框函数
			click_to_checkedall: function() {
				if(!this.isallchecked) {
					this.isallchecked = true
					for(let i = 0; i < this.tabledata.length; i++) {
						this.tabledata[i].ischecked = true
					}
				} else {
					this.isallchecked = false
					for(let i = 0; i < this.tabledata.length; i++) {
						this.tabledata[i].ischecked = false
					}
				}
			},
			click_to_checkedone:function(num){
				let flag = true
				if( this.tabledata[num].ischecked ){
					this.isallchecked = false
				}else{
					for(let i = 0; i < this.tabledata.length; i++){
						if( i != num ){
							flag = this.tabledata[i].ischecked && flag
						}
					}
					this.isallchecked = flag
				}
			},
		},
		mounted(){
			this.get_data_list({})
			// console.log(this.tabledata)
		},
		watch:{
			// 'search_data.pushIp':function(newval,old){
			// 	let pushIp = newval.join(".")
			// 	console.log(pushIp)
			// 	console.log(this.check_input(pushIp,"ip"))
			// }
		}
	}
</script>

<style>
	@import "../css/index.css";
	@import "../css/public_el.css";
</style>


